import { ApiPromise, WsProvider } from "@polkadot/api";
import { web3Accounts, web3Enable } from "@polkadot/extension-dapp";
import keyring from "@polkadot/ui-keyring";
import React, { useState, useEffect, createRef } from "react";
import { Container, Dimmer, Loader, Grid, Sticky } from "semantic-ui-react";

import "semantic-ui-css/semantic.min.css";

import AccountSelector from "./AccountSelector";
import BlockNumber from "./BlockNumber";
import TokenQuery from "./TokenQuery";
import DeveloperConsole from "./DeveloperConsole";
import Metadata from "./Metadata";
import NodeInfo from "./NodeInfo";

import Events from "./Events";
import Deposit from "./Deposit";
import Transfer from "./Transfer";
import Withdraw from "./Withdraw";

export default function App() {
  const [api, setApi] = useState();
  const [apiReady, setApiReady] = useState();
  const [accountLoaded, setAccountLoaded] = useState(false);
  const [accountAddress, setAccountAddress] = useState("");

  const WS_PROVIDER = "ws://127.0.0.1:9944";

  const accountPair = accountAddress && keyring.getPair(accountAddress);

  useEffect(() => {
    const provider = new WsProvider(WS_PROVIDER);

    const TYPES = {"TokenId": "u256"};

    ApiPromise.create({
      provider,
      types: TYPES
    })
      .then(api => {
        setApi(api);
        api.isReady.then(() => setApiReady(true));
      })
      .catch(e => console.error(e));
  }, []);

  // new hook to get injected accounts
  useEffect(() => {
    web3Enable("plasma-cash")
      .then(extensions => {
        // web3Account promise resolves with an array of injected accounts
        // or an empty array (e.g user has no extension, or not given access to their accounts)
        web3Accounts()
          .then(accounts => {
            // add the source to the name to avoid confusion
            return accounts.map(({ address, meta }) => ({
              address,
              meta: {
                ...meta,
                name: `${meta.name} (${meta.source})`
              }
            }));
          })
          // load our keyring with the newly injected accounts
          .then(injectedAccounts => {
            loadAccounts(injectedAccounts);
          })
          .catch(console.error);
      })
      .catch(console.error);
  }, []);

  const loadAccounts = injectedAccounts => {
    keyring.loadAll(
      {
        isDevelopment: true
      },
      injectedAccounts
    );
    setAccountLoaded(true);
  };

  const loader = function(text) {
    return (
      <Dimmer active>
        <Loader size="small">{text}</Loader>
      </Dimmer>
    );
  };

  if (!apiReady) {
    return loader("Connecting to the blockchain");
  }

  if (!accountLoaded) {
    return loader(
      "Loading accounts (please review any extension's authorization)"
    );
  }

  let accounts = keyring
                    .getPairs()
                    .map(account => ({
                        address: account.address,
                        name: account.meta.name.toUpperCase(),
                    }));

  const contextRef = createRef();

  return (
    <div ref={contextRef}>
      <Sticky context={contextRef}>
        <AccountSelector
          keyring={keyring}
          setAccountAddress={setAccountAddress}
          api={api}
        />
      </Sticky>
      <Container>
        <Grid divided>
          <Grid.Row columns={4} stretched>
            <NodeInfo api={api} />
            <Metadata api={api} />
            <BlockNumber api={api} />
            <BlockNumber api={api} finalized />
          </Grid.Row>
          <Grid.Row columns={3} stretched>
            <Grid.Column>
              <Transfer api={api} accountPair={accountPair} keyring={keyring} />
            </Grid.Column>
            <Grid.Column>
              <Deposit api={api} accountPair={accountPair} />
            </Grid.Column>
            <Grid.Column>
              <Withdraw api={api} accountPair={accountPair} />
            </Grid.Column>
          </Grid.Row>
          <Grid.Row columns={3} stretched>
            <Grid.Column>
              <TokenQuery accounts={accounts} api={api} />
            </Grid.Column>
            <Grid.Column>
              <Events api={api} />
            </Grid.Column>
          </Grid.Row>
        </Grid>
        {/* These components don't render elements. */}
        <DeveloperConsole api={api} />
      </Container>
    </div>
  );
}
