import React, { useState } from "react";

import { Grid, Form, Button, Input } from "semantic-ui-react";

export default function TokenQuery(props) {
  const { accounts, api } = props;

  const [output, setOutput] = useState("");

  const initialState = {
    tokenId: ""
  };
  const [formState, setFormState] = useState(initialState);
  const { tokenId } = formState;

  const onChange = (_, data) => {
    setFormState(formState => {
      return {
        ...formState,
        [data.state]: data.value
      };
    });
  };

  const runQuery = async () => {
    try {
      let result = await api.query.plasmaCash.tokens(tokenId);
      let owner_text;
      if (!result.isNone) {
          let account = accounts.filter(account => (account.address === result.toString()));
          owner_text = account.length === 1 ? "Owner: " + account[0].name : "Lookup failed!";
      } else {
          owner_text = "No one owns it!";
      }
      setOutput(owner_text);
    } catch (e) {
		setOutput(e.toString())
	}
  };

  return (
    <Grid.Column>
      <h1>Token Ownership</h1>
      <Form>
        <Form.Field>
          <Input
            onChange={onChange}
            label="Token ID"
            fluid
            placeholder="ID of Token"
            state="tokenId"
            type="text"
            value={tokenId}
          />
        </Form.Field>
        <Form.Field>
          <Button onClick={runQuery} primary type="submit">
            Query
          </Button>{" "}
          {output}
        </Form.Field>
      </Form>
    </Grid.Column>
  );
}
