import React, { useState } from "react";

import { Grid, Form, Dropdown, Input } from "semantic-ui-react";

import TxButton from "./TxButton";

export default function Extrinsics(props) {
  const { api, accountPair, keyring } = props;

  const [status, setStatus] = useState("");
  const [receiverId, setReceiverId] = useState("");

  // Get the list of accounts we possess the private key for
  const keyringOptions = keyring.getPairs().map(account => ({
    key: account.address,
    value: account.address,
    text: account.meta.name.toUpperCase(),
    icon: "user"
  }));

  const initialState = {
    tokenId: "",
  };

  const [formState, setFormState] = useState(initialState);
  const { tokenId } = formState;

  const onChange = (_, data) => {
    setFormState(formState => {
      return {
        ...formState,
        [data.state]: data.value
      };
    });
  };

  const onChangeReceiver = address => {
    setReceiverId(address);
  };

  return (
    <Grid.Column>
      <h1>Transfer Token</h1>
      <Form>
        <Form.Field>
          <Input
            onChange={onChange}
            label="Token ID"
            fluid
            placeholder="ID of Token you control"
            state="tokenId"
            type="text"
          />
        </Form.Field>
        <Form.Field>
          <Dropdown
            search
            selection
            clearable
            placeholder="Select Receipient"
            options={keyringOptions}
            onChange={(_, dropdown) => {
              onChangeReceiver(dropdown.value);
            }}
            value={receiverId}
          />
        </Form.Field>
        <Form.Field>
          <TxButton
            api={api}
            accountPair={accountPair}
            label={"Send"}
            params={[tokenId, receiverId]}
            setStatus={setStatus}
            tx={api.tx.plasmaCash.transfer}
          />
          {status}
        </Form.Field>
      </Form>
    </Grid.Column>
  );
}
